import axios from "axios";
import authHeader from "./auth-header";
import config from "../config";
import { professor_data, student_data, classroom_data } from "./fake-content";
import _t from "./i18n.service";

class BackendService {
  bearerConfig() {
    const user = JSON.parse(localStorage.getItem("user"));
    const config = {
      headers: { Authorization: `Bearer ${user.jwt}` },
    };
    return config;
  }

  updateJWT(res) {
    if (res.data.jwt) {
      const user = { ...JSON.parse(localStorage.getItem("user")), jwt: res.data.jwt };
      localStorage.setItem("user", JSON.stringify(user));
    }
  }

  async getClassrooms() {
    // FAKE return professor_data;

    let classrooms = await axios
      .get(config.API_URL + "/classrooms", {}, this.bearerConfig())
      .then(res => res.data.data)
      .catch(err => []);

    if (classrooms.length === 0) return [];

    // To force all promises to resolve before continuing, we chain them with map and wait for all to be resolved
    await Promise.all(
      classrooms.map(element => 
        axios
          .get(
            config.API_URL + "/questions/" + element.code,
            {},
            this.bearerConfig()
          )
          .then((res) => {
            element.questions = res.data.data;
          })
          .catch(err => {/* ignore errors */})
      )
    );

    return classrooms;
  }

  getStudentData(email) {
    // TODO API CALL
    return student_data;
  }

  getClassroomData(id) {
    return classroom_data;
  }

  newClassroom(title) {
    // TODO API CALL

    const classroom = {
      id: Math.random()
        .toString(16)
        .substr(2, 4),
      title: title,
      num_users: 0,
      questions: [],
    };

    return new Promise((resolve, reject) => {
      if (title.length > 0) {
        resolve({ classroom: classroom, message: _t("classroom_created") });
      } else {
        reject({ message: _t("creation_failed") });
      }
    });
  }

  delClassroom(code) {
    /* FAKE
    return new Promise((resolve, reject) =>
      resolve({ message: _t("classroom_deleted") })
    );
    */
    return new Promise((resolve, reject) => {
      axios
        .delete(
          config.API_URL + "/classroom/" + code,
          {},
          this.bearerConfig()
        )
        .then((res) => {
          this.updateJWT(res);
          resolve({ message: _t("classroom_deleted") });
        })
        .catch((err) => {
          reject({ message: _t("operation_failed") });
        });
    });
  }

  newQuestion(question) {
    // TODO API CALL
    return new Promise((resolve, reject) =>
      resolve({ message: _t("question_created"), question: question })
    );
  }

  delQuestion(id) {
    /* FAKE
    return new Promise((resolve, reject) =>
      resolve({ message: _t("question_deleted") })
    );
    */
    return new Promise((resolve, reject) => {
      axios
        .delete(
          config.API_URL + "/question/" + `${id}`,
          {},
          this.bearerConfig()
        )
        .then((res) => {
          this.updateJWT(res);
          resolve({ message: _t("question_deleted") });
        })
        .catch((err) => {
          reject({ message: _t("operation_failed") });
        });
    });
  }

  subscribeToClassroom(id) {
    // TODO API CALL
    return new Promise((resolve, reject) =>
      resolve({ message: _t("question_deleted") })
    );
  }

  answerQuestion(level, question) {
    // TODO API CALL
    return new Promise((resolve, reject) =>
      resolve({ message: _t("answer_recorded") })
    );
  }

  delUser(email, password) {
    return new Promise((resolve, reject) =>
      resolve({ message: _t("user_deleted") })
    );
  }

  changePassword(email, password, new_password) {
    /* FAKE
    return new Promise((resolve, reject) =>
      resolve({ message: _t("password_changed") })
    );
    */
    return new Promise((resolve, reject) => {
      axios
        .post(
          config.API_URL + "/changepassword",
          {
            email: email,
            password: password,
            newPassword: new_password,
          },
          this.bearerConfig()
        )
        .then((res) => {
          this.updateJWT(res);
          resolve({ message: _t("password_changed") });
        })
        .catch((err) => {
          reject({ message: _t("password_change_" + err.response.status) });
        });
    });
  }
}

export default new BackendService();
