import React, { Component } from "react";
import _t from "../../services/i18n.service";
import QuestionCard from "./question_card.component";
import ConfirmDeleteClassroom from "./confirm_delete_classroom.component";
import ConfirmDeleteQuestion from "./confirm_delete_question.component";
import BackendService from "../../services/backend.service";
import NewQuestion from "./new_question.component";

class Classroom extends Component {
  constructor(props) {
    super(props);
    this.onDelClassroom = this.onDelClassroom.bind(this);
    this.confirmDelQuestion = this.confirmDelQuestion.bind(this);
    this.onDelQuestion = this.onDelQuestion.bind(this);

    this.state = {
      show: "classroom",
      question: null,
    };
  }

  onDelClassroom() {
    this.props.onDelClassroom(this.props.classroom.code);
  }

  confirmDelQuestion(question) {
    this.setState({
      show: "confirmQuestion",
      question: question,
    });
  }

  onDelQuestion() {
    // TODO queda por determinar los parámetros exactos de esta llamada a la API
    BackendService.delQuestion(this.state.question).then(() => {
      this.props.onDelQuestion(this.state.question);
    });
  }

  render() {
    const classroom = this.props.classroom;
    console.log("render classroom");
    return (
      <div>
        {this.state.show === "confirmClassroom" && (
          <ConfirmDeleteClassroom
            classroom={classroom}
            onConfirm={(a) =>
              a ? this.onDelClassroom() : this.setState({ show: "classroom" })
            }
          />
        )}
        {this.state.show === "confirmQuestion" && (
          <ConfirmDeleteQuestion
            question={this.state.question}
            onConfirm={(a) =>
              a ? this.onDelQuestion() : this.setState({ show: "classroom" })
            }
          />
        )}
        {this.state.show === "newQuestion" && (
          <NewQuestion
            onNewQuestion={() => this.setState({ show: "classroom" })}
            onCancelNewQuestion={() => this.setState({ show: "classroom" })}
            classroom={classroom}
          />
        )}
        {this.state.show === "classroom" && (
          <div>
            <div className="card mt-4">
              <div className="card-header bg-primary text-light">
                <div className="row">
                  <div className="col-6 d-flex justify-content-start">
                    {classroom.code}
                  </div>
                  <div className="col-6 d-flex justify-content-end">
                    <i className="bi bi-person-fill"></i> {classroom.num_users}
                  </div>
                </div>
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-10">
                    <h5 className="card-title d-flex justify-content-start">
                      <span className="mx-2">
                        <i className="bi bi-easel"></i>
                      </span>{" "}
                      {classroom.title}
                    </h5>
                  </div>
                  <div className="col-2 d-flex justify-content-end">
                    <button
                      className="bi bi-trash btn btn-danger"
                      onClick={() =>
                        this.setState({ show: "confirmClassroom" })
                      }
                    >
                    </button>
                  </div>
                </div>
              </div>
            </div>

            <div className="row mt-5">
              <div className="col-6">
                <h5>{_t("questions")}</h5>
              </div>
              <div className="col-6 d-flex justify-content-end">
                <button
                  className="btn btn-success btn-block"
                  onClick={() => this.setState({ show: "newQuestion" })}
                >
                  <i className="bi bi-plus-circle"></i> {_t("add")}
                </button>
              </div>
            </div>
            {classroom.questions.map((q, i) => (
              <QuestionCard
                key={i}
                question={q}
                onDelete={this.confirmDelQuestion}
              />
            ))}
          </div>
        )}
      </div>
    );
  }
}

export default Classroom;
